<template>
  <div id="page-user-edit">
    <vx-card>
      	<div slot="no-body" class="tabs-container px-6 pt-6">
			<vs-tabs class="tab-action-btn-fill-conatiner">
				<!-- Account Tab -->
				<vs-tab label="Account" icon-pack="feather" icon="icon-user">
					<div class="tab-text">
						<div id="user-edit-tab-info">
							<!-- Avatar Row -->
							<div class="vx-row">
								<div class="vx-col">
									<div class="flex items-start flex-col sm:flex-row">
									<img :src="user_data.avatar" class="mr-8 rounded avatar-col" />
									<div>
										<p class="text-lg font-medium mb-2 mt-4 sm:mt-0">{{ user_data.name  }}</p>
										<input type="file" class="hidden" ref="update_avatar_input" @change="update_avatar" accept="image/*">
										<vs-button class="mr-4 mb-4" @click="$refs.update_avatar_input.click()">Change photo</vs-button>
										<vs-button type="border" color="danger">Delete photo</vs-button>
									</div>
									</div>
								</div>
							</div>
							<!-- Content Row -->
							<div class="vx-row">
								<div class="vx-col md:w-1/2 w-full">
									<vs-input class="w-full mt-4" label="Username" v-model="user_data.username" v-validate="'required|alpha_num'" name="username" />
									<span class="text-danger text-sm" v-show="errors.has('username')">{{ errors.first('username') }}</span>

									<vs-input class="w-full mt-4" label="Name" v-model="user_data.name" v-validate="'required|alpha_spaces'" name="name" />
									<span class="text-danger text-sm"  v-show="errors.has('name')">{{ errors.first('name') }}</span>

									<vs-input class="w-full mt-4" label="Email" type="email" v-model="user_data.email" v-validate="'required|email'" name="email" />
									<span class="text-danger text-sm"  v-show="errors.has('email')">{{ errors.first('email') }}</span>
								</div>

								<div class="vx-col md:w-1/2 w-full">
									<div class="mt-4">
										<label class="vs-input--label">Status</label>
										<v-select :clearable="false" :options="statusOptions" v-model="user_data.status" v-validate="'required'" name="status" :dir="$vs.rtl ? 'rtl' : 'ltr'" />
										<span class="text-danger text-sm"  v-show="errors.has('status')">{{ errors.first('status') }}</span>
									</div>
									<div class="mt-4">
										<label class="vs-input--label">Role</label>
										<v-select :clearable="false" :options="roleOptions" v-model="user_data.role" v-validate="'required'" name="role" :dir="$vs.rtl ? 'rtl' : 'ltr'" />
										<span class="text-danger text-sm"  v-show="errors.has('role')">{{ errors.first('role') }}</span>
									</div>
									<vs-input class="w-full mt-4" label="Company" v-model="user_data.company" v-validate="'alpha_spaces'" name="company" />
									<span class="text-danger text-sm"  v-show="errors.has('company')">{{ errors.first('company') }}</span>
								</div>
							</div>
							<!-- Permissions -->
							<vx-card class="mt-base" no-shadow card-border>
								<div class="vx-row">
									<div class="vx-col w-full">
									<div class="flex items-end px-3">
										<feather-icon svgClasses="w-6 h-6" icon="LockIcon" class="mr-2" />
										<span class="font-medium text-lg leading-none">Permissions</span>
									</div>
									<vs-divider />
									</div>
								</div>

								<div class="block overflow-x-auto">
									<table class="w-full">
									<tr>
										<th class="font-semibold text-base text-left px-3 py-2" v-for="heading in ['Module', 'Read', 'Write', 'Create', 'Delete']" :key="heading">{{ heading }}</th>
									</tr>

									<tr v-for="(val, name) in user_data.permissions" :key="name">
										<td class="px-3 py-2">{{ name }}</td>
										<td v-for="(permission, name) in val" class="px-3 py-2" :key="name+permission">
										<vs-checkbox v-model="val[name]" />
										</td>
									</tr>
									</table>
								</div>
							</vx-card>
							<!-- Save & Reset Button -->
							<div class="vx-row">
								<div class="vx-col w-full">
									<div class="mt-8 flex flex-wrap items-center justify-end">
									<vs-button class="ml-auto mt-2" @click="save_changes" :disabled="!validateForm">Save Changes</vs-button>
									<vs-button class="ml-4 mt-2" type="border" color="warning" @click="reset_data">Reset</vs-button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</vs-tab>
				<!-- Settings Tab -->
				<vs-tab label="Settings" icon-pack="feather" icon="icon-settings">
					<div class="tab-text">
						<p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Obcaecati ullam dicta aspernatur enim id. Vitae aperiam debitis fugiat rem repellendus quidem quas, quasi necessitatibus harum, iste unde officia incidunt ut.</p>
					</div>
				</vs-tab>
			</vs-tabs>
      	</div>
    </vx-card>
  </div>
</template>

<script>
import vSelect from 'vue-select'

export default {
	components: {
    	vSelect
  	},
	data() {
    return {

		user_data: {
			id: 1,
			avatar: 'https://i.pinimg.com/originals/1e/25/5d/1e255d7f708d38f0b8aa5400c8848c89.jpg',
			username: 'username',
			name: 'Mark Polo',
			email: 'mark.polo@gmail.com',
			status: 'active',
			role: 'admin',
			company: 'Logistics INC',
			birth_date: 'mm-dd-yy',
			mobile: '+321 09 123 45 67',
			languages: ["English", "Arabic"],
			gender: 'male',
			contact: '+123 45 678 90 12',
			permissions: {
				users: {
					read: true,
					write: false,
					create: false,
					delete: false
				},
				posts: {
					read: true,
					write: true,
					create: true,
					delete: true
				},
				comments: {
					read: true,
					write: false,
					create: true,
					delete: false
				}
			}
	  	},

		// data_local: JSON.parse(JSON.stringify(this.user_data)),

		statusOptions: [
			{ label: "Active",  value: "active" },
			{ label: "Blocked",  value: "blocked" },
			{ label: "Deactivated",  value: "deactivated" },
		],
		roleOptions: [
			{ label: "Admin",  value: "admin" },
			{ label: "User",  value: "user" },
			{ label: "Staff",  value: "staff" },
		],
    }
  },
  computed: {
    status_local: {
      get() {
        return { label: this.capitalize(this.data_local.status),  value: this.data_local.status  }
      },
      set(obj) {
        this.data_local.status = obj.value
      }
    },
    role_local: {
      get() {
        return { label: this.capitalize(this.data_local.role),  value: this.data_local.role  }
      },
      set(obj) {
        this.data_local.role = obj.value
      }
    },
    validateForm() {
      return !this.errors.any()
    }
  },
  methods: {
    capitalize(str) {
      return str.slice(0,1).toUpperCase() + str.slice(1,str.length)
    },
    save_changes() {
      if(!this.validateForm) return

      // Here will go your API call for updating data
      // You can get data in "this.data_local"

    },
    reset_data() {
      this.data_local = JSON.parse(JSON.stringify(this.data))
    },
    update_avatar() {
      // You can update avatar Here
      // For reference you can check dataList example
      // We haven't integrated it here, because data isn't saved in DB
    }
  }
}
</script>
<style lang="scss" scoped>
	.avatar-col {
		width: 8.5rem;
	}
</style>